<template>
  <div>
    <h1></h1>
    <el-table :data="list" style="width: 100%">
      <el-table-column prop="_id" label="ID"></el-table-column>
      <el-table-column prop="username" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="edit(scope.row._id)"></el-button>
          <el-button type="text" size="small" @click="del(scope.row._id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="total"
      :pageSize="10"
      :current-page.sync="currentPage"
      @current-change="getAdmin"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: "adminList",
  data() {
    return {
      //
      list: [],
      // 
      total: 0,
      // 
      currentPage: 1
    };
  },
  created() {
    this.getAdmin();
  },
  methods: {
    //
    getAdmin() {
      let url = "rest/admin_users";
      this.$http.get(url).then(res => {
        // let { code, data, totalCount } = res.data;
        // if (code === 1) {
        this.list = res.data;
        // this.total = totalCount;
        // }
      });
    },
    //
    edit(id) {
      this.$router.push(`/admin_users/edit/${id}`);
    },
    //
    del(id) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        let url = `rest/admin_users/${id}`;
        this.$http.delete(url).then(() => {
          this.getAdmin();
        });
      });
    }
  }
};
</script>