<template>
  <div>
    <h1></h1>
    <el-table :data="articleList" style="width: 100%">
      <el-table-column prop="_id" label="ID" width="240"></el-table-column>
      <el-table-column prop="title" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="edit(scope.row._id)"></el-button>
          <el-button type="text" size="small" @click="del(scope.row._id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="total"
      :pageSize="10"
      :current-page.sync="currentPage"
      @current-change="getArticleList"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: "articleList",
  data() {
    return {
      //
      articleList: [
        {
          title: "",
          body: "",
          categories: []
        }
      ],
      // 
      total: 0,
      // 
      currentPage: 1
    };
  },
  created() {
    this.getArticleList();
  },
  methods: {
    //
    getArticleList() {
      let url = "rest/articles";
      this.$http.get(url).then(res => {
        this.articleList = res.data;
        // this.total = totalCount;
      });
    },
    //
    edit(id) {
      this.$router.push(`/articles/edit/${id}`);
    },
    //
    del(id) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        let url = `rest/articles/${id}`;
        this.$http.delete(url).then(() => {
          this.getArticleList();
        });
      });
    }
  }
};
</script>
