<template>
  <div class="container">
    <h1>{{id ? '' : ''}}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <el-select v-model="article.categories" multiple>
          <el-option
            v-for="item in categories"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="article.title"></el-input>
      </el-form-item>
      <el-form-item label="">
        <vue-editor useCustomImageHandler @image-added="handleImageAdded" v-model="article.body"></vue-editor>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
//
import { VueEditor } from "vue2-editor";

export default {
  name: "articleCreate",
  props: {
    id: {}
  },
  components: {
    VueEditor
  },
  data() {
    return {
      //
      article: {
        title: "",
        body: "",
        categories: []
      },
      //
      categories: []
    };
  },
  created() {
    this.getCategories();
    //&&
    this.id && this.getInfo();
  },
  methods: {
    //
    getInfo() {
      let url = `rest/articles/${this.id}`;
      this.$http.get(url).then(res => {
        this.article = res.data;
      });
    },
    //
    getCategories() {
      let url = "rest/categories";
      this.$http.get(url).then(res => {
        this.categories = res.data;
      });
    },
    //
    async save() {
      if (this.id) {
        // 
        await this.$http.put(`/rest/articles/${this.id}`, this.article);
      } else {
        //
        await this.$http.post("/rest/articles", this.article);
      }
      this.$router.push("/articles/list");
      this.$message({
        type: "success",
        message: ""
      });
    },
    //
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      let data = new FormData();
      data.append("file", file);

      let res = await this.$http.post("upload", data);
      Editor.insertEmbed(cursorLocation, "image", res.data.url);
      resetUploader();
    }
  }
};
</script>