<template>
  <div class="main">
    <h1></h1>
    <el-table :data="list" style="width: 100%">
      <el-table-column prop="path" label=""></el-table-column>
      <el-table-column prop="imgUrl" label="">
        <template slot-scope="scope">
          <img class="carousel-img width-350" :src="scope.row.imgUrl" style="height:50px" />
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="edit(scope.row._id)"></el-button>
          <el-button type="text" size="small" @click="del(scope.row._id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "carouselList",
  data() {
    return {
      //
      list: []
    };
  },
  created() {
    this.getCarouselList();
  },
  methods: {
    //
    getCarouselList() {
      let url = "rest/ads";

      this.$http.get(url).then(res => {
        this.list = res.data;
      });
    },
    //
    edit(id) {
      this.$router.push(`/ads/edit/${id}`);
    },
    //
    del(id) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        let url = `rest/ads/${id}`;
        this.$http.delete(url).then(() => {
          this.getCarouselList();
        });
      });
    }
  }
};
</script>

