<template>
  <div>
    <h1>{{id ? '' : ''}}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <el-input v-model="info.username"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="info.password" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "adminCreate",
  props: {
    id: {}
  },
  data() {
    return {
      info: {
        //
        username: "",
        //
        password: ""
      }
    };
  },
  created() {
    //&&
    this.id && this.getInfo();
  },
  methods: {
    //
    getInfo() {
      let url = `rest/admin_users/${this.id}`;
      this.$http.get(url).then(res => {
        this.info = res.data;
      });
    },
    //
    save() {
      let { id, info } = this;
      let method;
      let url = "rest/admin_users";
      let data = info;

      if (id) {
        method = "put";
        url = `${url}/${id}`;
      } else {
        method = "post";
      }

      this.$http({
        method,
        url,
        data
      }).then(() => {
        this.$router.push("/admin_users/list");
      });
    }
  }
};
</script>