<template>
  <div style="text-align: left">
    <h3></h3>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="240"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="icon" label="">
        <template slot-scope="scope">
          <img :src="scope.row.icon" style="height:3rem" />
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <el-button
            @click="$router.push(`/items/edit/${scope.row._id}`)"
            type="text"
            size="small"
          ></el-button>
          <el-button type="text" @click="remove(scope.row)" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      items: []
    };
  },
  methods: {
    async fetch() {
      const res = await this.$http.get("/rest/items");
      //   console.log(res);
      this.items = res.data;
    },
    async remove(row) {
      this.$confirm(`${row.name}?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(async () => {
        await this.$http.delete(`/rest/items/${row._id}`);
        this.$message({
          type: "success",
          message: "!"
        });
        this.fetch();
      });
    }
  },
  created() {
    this.fetch();
  }
};
</script>
