<template>
  <div style="text-align: left">
    <h3>{{id?'':''}}</h3>
    <el-form ref="form" label-width="180px">
      <el-tabs type="border-card">
        <!--  -->
        <el-tab-pane label="">
          <el-form-item label="">
            <el-input v-model="formValue.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formValue.title"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-upload
              class="avatar-uploader banner"
              :headers="setHeader"
              :action="$http.defaults.baseURL+`upload`"
              :on-success="res => $set(formValue, 'banner', res.url)"
              :show-file-list="false"
            >
              <img width="100%" v-if="formValue.banner" :src="formValue.banner" class="icon" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="">
            <el-rate
              style="margin-top: 0.6rem"
              :max="9"
              show-score
              v-model="formValue.scores.difficult"
            ></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-rate
              style="margin-top: 0.6rem"
              :max="9"
              show-score
              v-model="formValue.scores.skills"
            ></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-rate
              style="margin-top: 0.6rem"
              :max="9"
              show-score
              v-model="formValue.scores.attach"
            ></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-rate
              style="margin-top: 0.6rem"
              :max="9"
              show-score
              v-model="formValue.scores.survive"
            ></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="formValue.items1" multiple>
              <el-option
                v-for="(item) in itemList"
                :key="item._id"
                :label="item.name"
                :value="item._id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="formValue.items2" multiple>
              <el-option
                v-for="(item) in itemList"
                :key="item._id"
                :label="item.name"
                :value="item._id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="formValue.usageTips"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="formValue.battleTips"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="formValue.teamTips"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="formValue.categories" multiple>
              <el-option
                v-for="item in categories"
                :label="item.name"
                :value="item._id"
                :key="item._id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-upload
              class="avatar-uploader"
              :headers="setHeader"
              :action="$http.defaults.baseURL+`upload`"
              :show-file-list="false"
              :on-success="afterUpload"
            >
              <img v-if="formValue.avatar" :src="formValue.avatar" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="">
          <el-button type="text" @click="addSkill">
            <i class="el-icon-plus"></i> 
          </el-button>
          <el-row type="flex" style="flex-wrap: wrap">
            <el-col
              style="margin-top: 20px;"
              :md="12"
              v-for="(item, index) in formValue.skills"
              :key="index"
            >
              <el-form-item label="">
                <el-input v-model="item.name"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="item.delay"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="item.cost"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-upload
                  class="avatar-uploader"
                  :headers="setHeader"
                  :action="uploadUrl"
                  :on-success="res => $set(item, 'icon', res.url)"
                  :show-file-list="false"
                >
                  <img v-if="item.icon" :src="item.icon" class="icon" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="">
                <el-input type="textarea" v-model="item.description"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="danger" @click="delSkill(index)"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>
      </el-tabs>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  props: {
    id: {
      type: String
    }
  },
  data() {
    return {
      formValue: {
        //
        name: "",
        //
        title: "",
        // 
        banner: "",
        //
        avatar: "",
        //
        categories: [],
        //
        items1: [],
        //
        items2: [],
        //
        scores: {
          difficult: 0,
          skills: 0,
          attach: 0,
          survive: 0
        },
        //
        usageTips: "",
        //
        battleTips: "",
        //
        teamTips: "",
        //
        skills: [],
        //
        rune: [],
        //
        partners: [],
        //
        controledBy: [],
        //
        control: []
      },
      //
      categories: [],
      //
      itemList: [],
      //
      heroList: [],
      //
      runeList: []
    };
  },
  methods: {
    afterUpload(res) {
      this.formValue.avatar = res.url;
    },
    async onSubmit() {
      if (this.id) {
        // 
        await this.$http.put(`/rest/heroes/${this.id}`, this.formValue);
      } else {
        //
        await this.$http.post("/rest/heroes", this.formValue);
      }
      this.$router.push("/heroes/list");
      this.$message({
        type: "success",
        message: ""
      });
    },
    fetch() {
      this.$http.get(`/rest/heroes/${this.id}`).then(res => {
        this.formValue = res.data;
      });
    },
    fetchParent() {
      this.$http.get(`/rest/heroes`).then(res => {
        this.parents = res.data;
      });
    },
    //
    getCategories() {
      this.$http.get("rest/categories").then(res => {
        this.categories = res.data;
      });
    },
    //
    getItemsList() {
      let url = "rest/items";
      this.$http.get(url).then(res => {
        this.itemList = res.data;
      });
    },
    //
    addSkill() {
      this.formValue.skills.push({});
    },
    //
    delSkill(index) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(async () => {
        this.formValue.skills.splice(index, 1);
      });
    }
  },
  created() {
    this.id && this.fetch();
    this.getCategories();
    this.getItemsList();
  }
};
</script>
<style scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>